import { Card, CardBody, Container, Row, Col } from "reactstrap";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import SubPageBackground from 'components/Common/SubPageBackground.js';
import ScrollToTopButton from 'components/Common/ScrollToTopButton.js';
import { getChangelog } from "../libs/ChangelogHelpers.js";
import { useEffect } from 'react';

export default function Changelog() {
  const changelogs = getChangelog();

  useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;

    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });

  return (
    <>
      <SubPageBackground>
        <ScrollToTopButton></ScrollToTopButton>

        <IndexNavbar isStatic={true} />
        <Container>
          <Col className="ml-auto mr-auto" md="8">
            <Card className="ectl-primary ectl-text">
              <CardBody>
                <h3>Changelog</h3>
                <Row>
                  <Col>
                    {changelogs.map((item, index) => {
                      return (
                        <>
                          <h5 key={index}>{item.date}</h5>
                          <ul className="text-left">
                            {item.log_list.map((logStr, index_2) => {
                              return (
                                <li key={index_2}>
                                  <p>{logStr}</p>
                                </li>
                              );
                            })}
                          </ul>
                          <br />
                        </>
                      );
                    })}
                  </Col>
                </Row>
                <p>For all previous changelogs, please check the <i>#changelog</i> channel in our <a href="https://discord.gg/dmrG9kXpxE">Discord server</a>.</p>
              </CardBody>
            </Card>
          </Col>
        </Container>
      </SubPageBackground>
      <DarkFooter />
    </>
  );
}